/* eslint-disable no-console */

import { register } from 'register-service-worker'
import api from '@/store/api'

if (process.env.NODE_ENV === 'production') {
  register(`${process.env.BASE_URL}service-worker.js`, {
    ready (reg) {
      reg.pushManager.getSubscription().then(pushSubscription => {
        // Subscribe if is a new Service
        subscribeNotifi(reg, pushSubscription)

        // Subscribe when grant permission
        const bc = new BroadcastChannel('bc1')

        bc.onmessage = (e) => {
          if (e.data === 'grant-notifi-perm') {
            subscribeNotifi(reg, pushSubscription)
          }
        }
      })
    },

    registered () {
      console.log('Service worker has been registered.')
    },

    cached () {
      console.log('Content has been cached for offline use.')
    },

    updatefound () {
      console.log('New content is downloading.')
    },

    updated () {
      console.log('New content is available; please refresh.')
    },

    offline () {
      console.log('No internet connection found. App is running in offline mode.')
    },

    error (error) {
      console.error('Error during service worker registration:', error)
    }
  })
}

function urlB64ToUint8Array(base64String) {
  const padding = '='.repeat((4 - base64String.length % 4) % 4)
  const base64 = (base64String + padding)
  // eslint-disable-next-line
    .replace(/\-/g, '+')
    .replace(/_/g, '/')

  const rawData = window.atob(base64)
  const outputArray = new Uint8Array(rawData.length)

  for (let i = 0; i < rawData.length; ++i) {
    outputArray[i] = rawData.charCodeAt(i)
  }
  return outputArray
}

function subscribeNotifi (reg, pushSubscription) {
  if (pushSubscription || Notification.permission != "granted") {
    return
  }

  const subscribeOptions = {
    userVisibleOnly: true,
    applicationServerKey: urlB64ToUint8Array(
      'BN4kljVkxbUiIhT8YbiEABdDXTVAtxvIaG9GmDyi7a8Ce8-DNIh8hHP_CGSVuRrzZyTqXqVM-WOmBVK4D4sOMq8'
    )
  }

  reg.pushManager.subscribe(subscribeOptions).then(PushSubscription => {
    sendToServer(PushSubscription)
  })
}

async function sendToServer (pushSubscription) {
  try {
    let result = await api.post('/notifi', pushSubscription)
    let data = await result.json()

    if (result.status !== 200) {
      throw new Error(data.error)
    }
  }
  catch (err) {
    console.log(err.message)
  }
}