<template>
<div class="container container-md">
    <div>
        <h3>Thông Con Báo</h3>

        <div v-if="learningDiary" class="notifi notifi-danger">
            Bạn đang học môn
            <router-link
            :to="'/study-diary/' + learningDiary.diary.sdtag">
                {{ learningDiary.diary.name }}
            </router-link>
        </div>

        <div class="notifi notifi-success">
            Thống kê ngày sẽ được tính vào lúc <b>23:59:50 mỗi ngày</b>!
        </div>

        <div class="notifi notifi-success">
            Thống kê tuần sẽ được tính vào lúc <b>23:59:50 mỗi thứ 7</b>!
        </div>

        <div class="notifi notifi-success">
            Ghi chú trong MTLKMS sử dụng cú pháp <b>Markdown</b> (MD)
        </div>
    </div>
    <br>

    <ChatBox />

    <div>
        <h3>Hoạt động mới</h3>
        <br>

        <div
        v-for="(diary, index) in diaries"
        :key="index">
            <div class="d-flex align-items-center">
                <img
                :src="getAvatarURL(diary.username)"
                :alt="diary.user + ' avatar'"
                class="avatar" />

                <div class="ml-2">
                    <router-link :to="'/users/' + diary.username">
                        {{ diary.user }}
                    </router-link>

                    vừa học
                    <b>{{ caculateTime(diary.time) }}</b>
                    <br>

                    trong môn
                    <router-link :to="'/study-diary/' + diary.sdtag">
                        {{ diary.name }}
                    </router-link>
                    <br>

                    <div class="tag">{{ caculatePassedTime(diary.stop_at) }}</div>
                </div>
            </div>
            
            <p>
                Bắt đầu:
                <b>{{ new Date(diary.start_at).toLocaleString('vi-VN') }}</b>
                <br>
                Kết thúc: 
                <b>{{ new Date(diary.stop_at).toLocaleString('vi-VN') }}</b>
            </p>
            
            <p class="quote" v-html="parseMarkdown(diary.log)"></p>

            <br><hr><br>
        </div>
    </div>

    <RequestNotifi />
</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import api from '@/store/api'
import parseMarkdown from '@/js/parseMarkdown'
import ChatBox from '@/components/ChatBox'
import RequestNotifi from '@/components/RequestNotifi.vue'

export default {
    name: 'HomeView',

    components: {
        ChatBox,
        RequestNotifi
    },

    data () {
        return {
            diaries: []
        }
    },

    created () {
        if (!this.loadSessionData()) {
            this.fetchDiaries()
        }
    },

    computed: {
        ...mapState(['learningDiary'])
    },

    methods: {
        ...mapMutations(['setIsLoading']),

        async fetchDiaries () {
            try {
                this.setIsLoading(true)

                let result = await api.get('/study-diary/diary/all')
                let data = await result.json()

                this.setIsLoading(false)

                if (result.status != 200) throw new Error(data.error)

                this.diaries = data.data
                this.saveSessionData()
            }
            catch (err) {
                this.setIsLoading(false)
                throw err
            }
        },

        caculateTime (minutes) {
            let hours = (minutes / 60).toFixed(2)

            return `${minutes} phút (${hours} giờ)`
        },

        caculatePassedTime (timeEnd) {
            let dateNow = new Date()
            let dateEnd = new Date(timeEnd)

            // Year
            let yearNow = dateNow.getFullYear()
            let yearEnd = dateEnd.getFullYear()
            let yearDiff = yearNow - yearEnd

            if (yearDiff !== 0) {
                return yearDiff + ' năm trước'
            }

            // Month
            let monthNow = dateNow.getMonth()
            let monthEnd = dateEnd.getMonth()
            let monthDiff = monthNow - monthEnd

            if (monthDiff !== 0) {
                return monthDiff + ' tháng trước'
            }

            // Day
            let dayNow = dateNow.getDate()
            let dayEnd = dateEnd.getDate()
            let dayDiff = dayNow - dayEnd

            if (dayDiff !== 0) {
                return dayDiff + ' ngày trước'
            }

            // Hour
            let hourNow = dateNow.getHours()
            let hourEnd = dateEnd.getHours()
            let hourDiff = hourNow - hourEnd

            if (hourDiff !== 0) {
                return hourDiff + ' giờ trước'
            }

            // Minute
            let minuteNow = dateNow.getMinutes()
            let minuteEnd = dateEnd.getMinutes()
            let minuteDiff = minuteNow - minuteEnd

            return minuteDiff + ' phút trước'
        },

        getTimeDiff (start, end) {
            let startTime = new Date(start)
            let stopTime = new Date(end)
            let timeDiff = stopTime.getTime() - startTime.getTime()

            return {
                seconds: timeDiff / 1000,
                minutes: timeDiff / 60000,
                hours: timeDiff / 3600000
            }
        },

        saveSessionData () {
            sessionStorage.setItem(
                'homeviewDiaries',
                JSON.stringify(this.diaries)
            )
        },

        loadSessionData () {
            let sessionData = sessionStorage.getItem('homeviewDiaries')
            if (!sessionData) return false

            let data = JSON.parse(sessionData)

            this.diaries = data

            return true
        },

        getAvatarURL (username) {
            return api.getAvatarURL(username)
        },

        parseMarkdown (text) {
            return parseMarkdown(text)
        }
    }
}
</script>

<style scoped>
.tag {
    background-color: #ff907f;
    color: #fff;
    display: inline-block;
    padding: .25rem .5rem;
    border-radius: 5px;
    margin-top: .25rem;
    font-style: italic;
}
</style>