<template>
    <div class="chat-container" ref="chatbox">
        <div
        v-for="(msg, index) of chatMessages"
        :key="'chat' + index">
            <div :align="isAuth(msg.user.username) ? 'right' : 'left'">
                <div
                :class="{
                    chatMsg: true,
                    chatMsgAuth: isAuth(msg.user.username),
                    chatMsgOther: !isAuth(msg.user.username)
                }">

                    <img
                    :src="getAvatarURL(msg.user.username)"
                    :alt="msg.user.username"
                    class="avatar-sm">

                    <div>
                        <router-link
                        :to="'/users/' + msg.user.username">
                            <b :style="{color: msg.user.color}">{{ msg.user.name }}</b>
                        </router-link>
                        <br>

                        <small class="text-secondary">
                            {{ new Date(msg.time).toLocaleString('vi-VN') }}
                        </small>

                        <p>{{ msg.message }}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>

    <form class="chat-input" @submit.prevent="send">
        <label class="input">
            <span class="material-icons">chat</span>

            <input
                type="text"
                name="message"
                placeholder="Message..."
                v-model="chatMessage"
                required
            />
        </label>

        <button class="btn">
            <span class="material-icons">send</span>
        </button>
    </form>

    <message-popup
    :title="message.title"
    :message="message.message"
    :type="message.type"
    :is-display="message.isDisplay" />
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import api from '@/store/api'
import { io } from "socket.io-client"
import MessagePopup from '@/components/MessagePopup.vue'

export default {
    name: 'ChatBox',

    components: {
        MessagePopup
    },

    created () {
        this.socket = io(api.host)

        this.loadMessages()
        this.socketEvents()
    },

    unmounted () {
        this.socket.disconnect()
    },

    data () {
        return {
            chatMessage: '',
            chatMessages: [],

            message: {
                title: '',
                message: '',
                type: '',
                isDisplay: false
            }
        }
    },

    computed: {
        ...mapState(['user'])
    },

    methods: {
        ...mapMutations(['setIsLoading']),

        async loadMessages () {
            this.setIsLoading(true)

            try {
                let result = await api.get('/chat')
                let data = await result.json()

                this.setIsLoading(false)

                if (result.status !== 200) {
                    throw new Error(data.error)
                }

                this.chatMessages = data
                setTimeout(() => this.scrollToBottom(), 250)
            }
            catch (e) {
                this.setIsLoading(false)

                this.showMessage(
                    'Lỗi',
                    e.message,
                    'error'
                )
            }
        },

        async send () {
            this.setIsLoading(true)

            try {
                let result = await api.post('/chat', {message: this.chatMessage})
                let data = await result.json()

                this.setIsLoading(false)

                if (result.status !== 200) {
                    throw new Error(data.error)
                }
            }
            catch (e) {
                this.setIsLoading(false)

                this.showMessage(
                    'Lỗi',
                    e.message,
                    'error'
                )
            }
        },

        socketEvents () {
            this.socket.on('message', (data) => {
                this.chatMessages.push(data)

                setTimeout(() => this.scrollToBottom(), 250)

                this.chatMessage = ''
            })
        },

        getAvatarURL (username) {
            return api.getAvatarURL(username)
        },

        showMessage (title, message, type) {
            this.message.title = title
            this.message.message = message
            this.message.type = type
            this.message.isDisplay = !this.message.isDisplay
        },

        isAuth (username) {
            if (username == this.user.username) return true
            return false
        },

        scrollToBottom() {
            const el = this.$refs.chatbox
            
            el.scrollTop = el.scrollHeight + 120
        }
    }
}

</script>

