    display: flex;
    padding: .75rem 1rem;
    border-radius: 5px;
    width: fit-content;
    max-width: 85%;
    margin-bottom: 1rem;
}

.chatMsgAuth {
    justify-content: end;
    flex-direction: row-reverse;
    align-items: flex-start;
    background-color: #ffccff;
    color: #000;
}

.chatMsgOther {
    justify-content: start;
    align-items: flex-start;
    background-color: #fff3f1;
    color: #000;
}

.chatMsg div, p {
    padding: 0;
    margin: 0;
}

.chatMsg p {
    text-align:justify;
}

.chatMsg a {
    text-decoration: none;
}

.chatMsgOther img {
    margin-top: .25rem;
    margin-right: .5rem;
}

.chatMsgAuth img {
    margin-top: .25rem;
    margin-left: .5rem;
}

.chatMsgAuth b {
    color: #ff2200;
}

.chat-input {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
}

.chat-input .btn {
    background-color: #ff907f10;
    padding: .51rem;
    border-radius: 0 5px 5px 0;
    color: #ff90ff;
    transition: color .5s;
    margin-top: 0;
}

.chat-input .btn:hover {
