<template>
<div class="container container-sm">
    <transition name="fade-in">
        <form action="" v-if="!isStartSendEmail" @submit.prevent="sendEmail">
            <h3>Email của bạn:</h3>
            <p class="text-secondary">Một mã xác nhận sẽ được gửi tới email của bạn. Nếu không tìm thấy, vui lòng kiểm tra bao gồm hòm thư spam.</p>

            <label class="input">
                <span class="material-icons">mail</span>
                <input name="email" type="email" required placeholder="Email" v-model="email">
                <p>Email</p>
            </label>

            <button class="btn btn-primary mt-4">
                <span class="material-icons mr-2">send</span>
                Next
            </button>
        </form>
    </transition>

    <transition name="fade-in">
        <form action="" v-if="isSendEmail" @submit.prevent="updatePassword">
            <h3>Nhập mã xác nhận</h3>
            <p class="text-secondary">Nhập mã xác nhận vừa được gửi đến email của bạn. Mã xác nhận có hiệu lực trong 5 phút.</p>

            <label class="input">
                <span class="material-icons">code</span>
                <input name="verificationCode" type="text" required placeholder="Code" v-model="form.verificationCode">
                <p>Code</p>
            </label>
            <br><hr><br>

            <h3>Đổi mật khẩu</h3>
            <p class="text-secondary">Mật khẩu phải từ 6 ký tự, bao gồm chữ in hoa, chữ in thường, số và kí tự đặc biệt.</p>

            <label class="input">
                <span class="material-icons">lock</span>
                <input type="password" required placeholder="New Password" v-model="form.newPassword">
                <p>New Password</p>
            </label>

            <label class="input">
                <span class="material-icons">lock</span>
                <input type="password" required placeholder="Re-Password" v-model="form.rePassword">
                <p>Re-Password</p>
            </label>

            <button class="btn btn-primary mt-4">
                <span class="material-icons mr-2">send</span>
                Next
            </button>
        </form>
    </transition>

    <message-popup
    :title="message.title"
    :message="message.message"
    :type="message.type"
    :is-display="message.isDisplay" />
</div>
</template>

<script>
import api from '@/store/api'
import { mapMutations } from 'vuex'
import MessagePopup from '@/components/MessagePopup.vue'

export default {
    name: 'ForgetPwdView',

    components: {
        MessagePopup
    },

    data () {
        return {
            isStartSendEmail: false,
            isSendEmail: false,
            email: '',
            
            form: {
                verificationCode: '',
                newPassword: '',
                rePassword: ''
            },

            message: {
                title: '',
                message: '',
                type: '',
                isDisplay: false
            }
        }
    },

    methods: {
        ...mapMutations(['setIsLoading']),

        async sendEmail () {
            try {
                this.isStartSendEmail = true
                this.setIsLoading(true)

                let result = await api.post('/forget-password', {
                    email: this.email
                })

                let data = await result.json()

                this.setIsLoading(false)

                if (result.status !== 200) {
                    throw new Error(data.error)
                }

                this.isSendEmail = true
            }
            catch (err) {
                this.isStartSendEmail = false
                this.setIsLoading(false)

                if (err.message == 'Email not found') {
                    this.showMessage(
                        'Lỗi',
                        'Không tìm thấy người dùng',
                        'error'
                    )
                }
                else {
                    this.showMessage(
                        'Lỗi',
                        'Không thể kết nối đến máy chủ',
                        'error'
                    )
                }
            }
        },

        async updatePassword () {
            if (this.form.newPassword !== this.form.rePassword) {
                this.showMessage(
                    'Lỗi',
                    'Mật khẩu không khớp',
                    'warning'
                )

                throw new Error('Password does not match')
            }

            try {
                this.setIsLoading(true)

                let result = await api.put('/forget-password', {
                    email: this.email,
                    validateCode: this.form.verificationCode,
                    newPassword: this.form.newPassword
                })

                let data = await result.json()

                this.setIsLoading(false)

                if (result.status != 200) {
                    throw new Error(data.error)
                }

                this.showMessage(
                    'Thành công!',
                    'Cập nhật mật khẩu mới thành công!',
                    'success'
                )

                setTimeout(() => {
                    location.href = '/login'
                }, 3000)
            }
            catch (err) {
                this.setIsLoading(false)

                let msg = ''

                if (err.message == 'Email not found') msg = 'Không tìm thấy người dùng'
                else if (err.message == 'Code has expired') msg = 'Mã bảo mật đã hết hạn'
                else if (err.message == 'Code is not valid') msg = 'Mã bảo mật không chính xác'  
                else if (err.message == 'Update Password failed') msg = 'Cập nhật mật khẩu không thành công, vui lòng thử lại sau'
                else if (err.message == 'Invalid password') msg = 'Mật khẩu phải từ 6 kí tự, bao gồm chữ in hoa, chữ in thường, số và kí tự đặc biệt'
                else msg = 'Không thể kết nối đến máy chủ'

                this.showMessage(
                    'Lỗi',
                    msg,
                    'error'
                )
            }
        },

        showMessage (title, message, type) {
            this.message.title = title
            this.message.message = message
            this.message.type = type
            this.message.isDisplay = !this.message.isDisplay
        }
    }
}
</script>